// import axios from "axios";

let lsSinyal = JSON.parse(localStorage.getItem("mw_sinyal"));

const state = {
  sinyal: lsSinyal != null ? lsSinyal.sinyal : [],
  lastFetch: lsSinyal != null ? lsSinyal.lastFetch : null,
};

const getters = {
  listSinyal(state) {
    return state.sinyal;
  },
  lastFetchSinyal(state) {
    return state.lastFetch != null ? new Date(state.lastFetch).toLocaleString("id-ID") : "";
  },
};

const actions = {
  registerSinyal(context) {
    let sinyal = [];
    console.log("sinyal", sinyal);
    context.rootState.coin.coins.forEach(item => {
      let askPrice = context.rootGetters.getBookCoinPriceAsk(item.nama_coin);
      let bidPrice = context.rootGetters.getBookCoinPriceBid(item.nama_coin);
      sinyal.push({
        symbol: item.nama_coin,
        pair: askPrice.pair,
        modal_kiri: context.rootState.setting.settings.modal_kiri || 400,
        modal_kanan: context.rootState.setting.settings.modal_kanan || 400,
        coin_address: item.coin_address,
        binance_ask_price: askPrice.price,
        binance_bid_price: bidPrice.price,
        inch_usdt: 0,
        selisih_pnl_kiri: 0,
        selisih_pnl_kiri_persen: 0,
        inch_sum: 0,
        inch_price: 0,
        selisih_pnl_kanan: 0,
        selisih_pnl_kanan_persen: 0,
      });
    });
    console.log(sinyal);
    context.commit("registerSinyal", sinyal);
  },
  updateSinyal(context, coin) {
    let sinyal = state.sinyal;
    let index = state.sinyal.findIndex(item => {
      return item.symbol == coin.symbol;
    });
    let symbol = sinyal[index];
    symbol = { ...symbol, ...coin.value };
    sinyal.splice(index, 1, symbol);
    context.commit("registerSinyal", sinyal);
  },
  clearSinyal(context) {
    let sinyal = state.sinyal.map(item => {
      return {
        ...item,
        binance_ask_price: "<div class='spinner-border spinner-border-sm'></div>",
        binance_bid_price: "<div class='spinner-border spinner-border-sm'></div>",
        inch_usdt: "<div class='spinner-border spinner-border-sm'></div>",
        selisih_pnl_kiri: "<div class='spinner-border spinner-border-sm'></div>",
        selisih_pnl_kiri_persen: "<div class='spinner-border spinner-border-sm'></div>",
        inch_sum: "<div class='spinner-border spinner-border-sm'></div>",
        inch_price: "<div class='spinner-border spinner-border-sm'></div>",
        selisih_pnl_kanan: "<div class='spinner-border spinner-border-sm'></div>",
        selisih_pnl_kanan_persen: "<div class='spinner-border spinner-border-sm'></div>",
      };
    });
    // sinyal.filter(item => {{
    // }});
    context.commit("registerSinyal", sinyal);
  },
};

const mutations = {
  registerSinyal(state, payload) {
    localStorage.setItem("mw_sinyal", JSON.stringify({ sinyal: payload, lastFetch: Date.now() }));
    state.sinyal = payload;
    state.lastFetch = Date.now();
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
