import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/dashboard",
      component: () => import("@/view/layout/Layout"),
      children: [
        {
          path: "/dashboard",
          name: "dashboard",
          component: () => import("@/view/pages/Dashboard.vue"),
        },
        {
          path: "/dashboard_erc",
          name: "dashboard_erc",
          component: () => import("@/view/pages/DashboardErc.vue"),
        },
        {
          path: "/user_manager",
          name: "user_manager",
          component: () => import("@/view/pages/user/UserManager.vue"),
        },
        {
          path: "/setting",
          name: "setting",
          component: () => import("@/view/pages/Setting.vue"),
        },
        {
          path: "/data",
          name: "data",
          component: () => import("@/view/pages/Data.vue"),
        },
      ],
    },
    {
      path: "/",
      component: () => import("@/view/pages/auth/login_pages/Login-1"),
      children: [
        {
          name: "login",
          path: "/login",
          component: () => import("@/view/pages/auth/login_pages/Login-1"),
        },
        {
          name: "register",
          path: "/register",
          component: () => import("@/view/pages/auth/login_pages/Login-1"),
        },
      ],
    },
    {
      path: "*",
      redirect: "/404",
    },
    {
      // the 404 route, when none of the above matches
      path: "/404",
      name: "404",
      component: () => import("@/view/pages/error/Error-1.vue"),
    },
    {
      // the 404 route, when none of the above matches
      path: "/restricted",
      name: "restricted",
      component: () => import("@/view/pages/error/ErrorRestricted.vue"),
    },
  ],
});
